<template><section><h2>Upload 上传图片</h2>
<p>暂时仅支持图片上传，以后会扩展成多文件上传。</p>
<h3>基本用法</h3>
<p>可通过width设置尺寸</p>
<div class="demo-block mt_15">
<tj-upload class="mr_10" v-model="imageUrl" action="https://sixtest.baojunev.com/api/v2/common/uploadOneFile"></tj-upload>
<tj-upload v-model="imageUrl1" action="https://sixtest.baojunev.com/api/v2/common/uploadOneFile"></tj-upload>

</div>
<demo-block>
                    <div slot="desc"><tj-upload v-model="imageUrl" action="https://sixtest.baojunev.com/api/v2/common/uploadOneFile"></tj-upload>

</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tj-upload</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"imageUrl"</span> <span class="hljs-attr">action</span>=<span class="hljs-string">"https://sixtest.baojunev.com/api/v2/common/uploadOneFile"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">tj-upload</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">imageUrl</span>: <span class="hljs-string">""</span>,
        <span class="hljs-attr">imageUrl1</span>: <span class="hljs-string">"https://img.yzcdn.cn/vant/cat.jpeg"</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>进度设置</h3>
<p>通过is-progress 显示进度条可设置line、circle两种状态，默认为line。</p>
<div class="demo-block mt_15">
<tj-upload class="mr_10" v-model="imageUrl2" is-progress action="https://sixtest.baojunev.com/api/v2/common/uploadOneFile">
</tj-upload>
<tj-upload v-model="imageUrl3" is-progress progress-type="circle" action="https://sixtest.baojunev.com/api/v2/common/uploadOneFile">
</tj-upload>
</div>
<demo-block>
                    <div slot="desc"><tj-upload v-model="imageUrl2" is-progress action="https://sixtest.baojunev.com/api/v2/common/uploadOneFile">
</tj-upload>
<tj-upload v-model="imageUrl3" is-progress progress-type="circle" action="https://sixtest.baojunev.com/api/v2/common/uploadOneFile">
</tj-upload>

</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tj-upload</span> 
  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"imageUrl2"</span> 
  <span class="hljs-attr">is-progress</span> 
  <span class="hljs-attr">action</span>=<span class="hljs-string">"https://sixtest.baojunev.com/api/v2/common/uploadOneFile"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">tj-upload</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">tj-upload</span> 
  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"imageUrl3"</span> 
  <span class="hljs-attr">is-progress</span> 
  <span class="hljs-attr">progress-type</span>=<span class="hljs-string">"circle"</span>
  <span class="hljs-attr">action</span>=<span class="hljs-string">"https://sixtest.baojunev.com/api/v2/common/uploadOneFile"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">tj-upload</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">imageUrl2</span>: <span class="hljs-string">""</span>,
        <span class="hljs-attr">imageUrl3</span>: <span class="hljs-string">""</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>回调事件</h3>
<div class="demo-block mt_15">
<tj-upload v-model="imageUrl4" is-progress :before-upload="beforeUpload" :on-progress="onProgress" :on-success="onSuccess" action="https://sixtest.baojunev.com/api/v2/common/uploadOneFile">
</tj-upload>
</div>
<demo-block>
                    <div slot="desc"><tj-upload v-model="imageUrl" action="https://sixtest.baojunev.com/api/v2/common/uploadOneFile"></tj-upload>

</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tj-upload</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"imageUrl"</span> <span class="hljs-attr">action</span>=<span class="hljs-string">"https://sixtest.baojunev.com/api/v2/common/uploadOneFile"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">tj-upload</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">imageUrl4</span>: <span class="hljs-string">""</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      beforeUpload(file) {
        <span class="hljs-keyword">const</span> isPng = file.type === <span class="hljs-string">'image/png'</span>;
        <span class="hljs-keyword">const</span> isLt2M = file.size / <span class="hljs-number">1024</span> / <span class="hljs-number">1024</span> &lt; <span class="hljs-number">2</span>;
        <span class="hljs-keyword">if</span> (!isPng) {
          <span class="hljs-keyword">this</span>.$toast(<span class="hljs-string">'上传头像图片只能是 PNG 格式!'</span>);
        }
        <span class="hljs-keyword">if</span> (!isLt2M) {
          <span class="hljs-keyword">this</span>.$toast(<span class="hljs-string">'上传头像图片大小不能超过 2MB!'</span>);
        }
        <span class="hljs-keyword">return</span> isPng &amp;&amp; isLt2M;
      },
      onProgress(event) {
        <span class="hljs-built_in">console</span>.log(event)
      },
      onSuccess(data) {
        <span class="hljs-keyword">this</span>.$toast(<span class="hljs-string">'上传成功!'</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Props</h3>
<div class="demo-block table-wrap">
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>width</td>
<td>容器宽度</td>
<td>number</td>
<td></td>
<td>100</td>
</tr>
<tr>
<td>is-progress</td>
<td>是否显示进度</td>
<td>boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>progress-type</td>
<td>进度条的宽度</td>
<td>number</td>
<td>line，circle</td>
<td>line</td>
</tr>
<tr>
<td>before-upload</td>
<td>上传图片之前的钩子，参数为上传的图片，若返回 false 停止上传</td>
<td>function(file)</td>
<td></td>
<td></td>
</tr>
<tr>
<td>on-progress</td>
<td>图片上传时的钩子</td>
<td>function(event)</td>
<td></td>
<td></td>
</tr>
<tr>
<td>on-success</td>
<td>图片上传成功时的钩子</td>
<td>function(data)</td>
<td></td>
<td></td>
</tr>
<tr>
<td>on-error</td>
<td>图片上传失败时的钩子</td>
<td>function(orroe)</td>
<td></td>
<td></td>
</tr>
</tbody>
</table>
</div></section></template>

<script>
  export default {
    data() {
      return {
        imageUrl: "",
        imageUrl1: "https://img.yzcdn.cn/vant/cat.jpeg",
        imageUrl2: "",
        imageUrl3: "",
        imageUrl4: ""
      }
    },
    methods: {
      beforeUpload(file) {
        const isPng = file.type === 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isPng) {
          this.$toast('上传头像图片只能是 PNG 格式!');
        }
        if (!isLt2M) {
          this.$toast('上传头像图片大小不能超过 2MB!');
        }
        return isPng && isLt2M;
      },
      onProgress(event) {
        console.log(event)
      },
      onSuccess(data) {
        this.$toast('上传成功!')
      }
    }
  }
</script>


